.sidenav {
    z-index: 1;
    top: 0;
    left: 0;
    overflow-x: hidden;
  }
  
  .sidenav a {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 1rem;
    color: black;
    display: block;
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.15), 0 10px 20px 0 rgba(0, 0, 0, 0.10);
  }
  
  .sidenav a:hover {
    background-color: lightskyblue;
  }
  
  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }
